.theme-dark {

  // Changes color code of dark color
  --vs-dark: 184,194,204;

  background-color: $content-dark-bg;
  color: $theme-dark-text-color !important;

  .main-vertical {
    &.navbar-static {
      .vx-navbar-wrapper {
        background: none;
      }
    }
  }

  .main-horizontal {
    .menu-item {
      .h-nav-menu-item {
        &.disabled-item {
          opacity: .3;
        }
      }
    }

    .vx-navbar-wrapper {
      &:not(.nav-menu-wrapper) {
        @media (min-width: 1200px) {
          background: none !important;
        }
      }
    }

    &.navbar-sticky {
      .vs-navbar {
        &:not(.vx-navbar) {
          // body dark bg
          background-color: $theme-light-dark-bg !important;
        }
      }
    }

    &.navbar-static {
      .vs-navbar {
        &:not(.vx-navbar) {
          &.d-theme-dark-light-bg {
            background-color: $theme-light-dark-bg !important;
          }
        }
      }
    }
  }


  .content-area__heading {
    h2 {
      color: $theme-dark-heading-color;
    }
  }

  code {
    color: $grey;
    background: $content-dark-bg;
  }

  pre[class*="language-"] {
    background-color: $dark;
    text-shadow: none;

    code {
      background-color: transparent;

      &[class*="language-"] {
        text-shadow: none;
      }
    }
  }

  // Label
  label {
    color: $theme-dark-text-color;
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    color: $theme-dark-heading-color;

  }

  ul.bordered-items {
    li {
      border-color: $theme-dark-border-color !important;
    }
  }

  // Notification
  .notification-dropdown,
  .cart-dropdown {

    .notification:hover,
    .cart-item:hover {
      background: $dark-card-color;
    }

    .checkout-footer,
    .notification-footer {
      background-color: $theme-light-dark-bg;
    }

    .checkout-footer:hover,
    .notification-footer:hover {
      background-color: $theme-dark-bg;
    }
  }

  .vs-component {
    &.vs-notifications {
      &.vs-noti-dark {
        color: $black !important;

        .filling {
          background: $grey !important;
        }

        h3 {
          color: $black !important;
        }
      }
    }
  }

  // Card
  .vx-card {
    background-color: $dark-card-color;

    &.card-border {
      border-color: $theme-dark-border-color;
    }

    .vx-card__title {
      h4 {
        color: $theme-dark-heading-color;
      }
    }

    .con-vs-alert-dark {
      color: $grey-light;
    }

    .code-content {
      pre[class^="language-"] {
        background-color: $content-dark-bg;
        text-shadow: none;

        code {
          background-color: transparent;

          &[class*="language-"] {
            text-shadow: none;
          }
        }
      }
    }

    .vx-card__body {
      .con-vs-alert-primary {
        h4 {
          color: rgba(var(--vs-primary), 1);
        }
      }

      .con-vs-alert-success {
        h4 {
          color: rgba(var(--vs-success), 1);
        }
      }

      .con-vs-alert-danger {
        h4 {
          color: rgba(var(--vs-danger), 1);
        }
      }

      .con-vs-alert-info {
        h4 {
          color: rgba(var(--vs-info), 1);
        }
      }

      .con-vs-alert-warning {
        h4 {
          color: rgba(var(--vs-warning), 1);
        }
      }
    }

    // Card-analytics
    .tasks-today-container {
      .tasks-today__task {
        &:hover {
          background: $table-dark-stripe !important;
        }
      }
    }
  }

  .vx-navbar-wrapper {
    background: linear-gradient(to bottom, rgba(44, 48, 60, 0.9) 44%, rgba(44, 48, 60, 0.43) 73%, rgba(44, 48, 60, 0) 100%);

    .vs-navbar {
      .search-full-container {
        background-color: $dark-card-color !important;

        .vx-auto-suggest {
          input {
            background: $dark-card-color;
          }
        }
      }

      .vx-auto-suggest {
        .auto-suggest-suggestions-list {
          .auto-suggest__suggestion-group__suggestion {
            &.vx-auto-suggest__current-selected {
              background: $dark-card-color;
              color: $white;
            }

            color: $theme-dark-text-color;
          }
        }
      }
    }
  }

  // Vertical NavMenu
  .v-nav-menu {
    .vs-sidebar {
      background-color: $sidebar-dark-bg;
    }

    .shadow-bottom {
      width: 94%;
      background: linear-gradient(to bottom, rgb(15, 22, 66) 44%, rgba(15, 22, 66, 0.51) 73%, rgba(44, 48, 60, 0) 100%)
    }

    .scroll-area-v-nav-menu {

      .feather-icon,
      span:not(.vs-chip--text) {
        color: $theme-dark-text-color;
      }

      a {
        &:not(.router-link-active) {
          .feather-icon {

            svg,
            span {
              color: $theme-dark-text-color;
            }
          }
        }

        &.router-link-active {
          span {
            color: $white;
          }
        }
      }

      .vs-sidebar-group {
        &.vs-sidebar-group-active {
          >.group-header {
            background: $content-dark-bg;
          }
        }

        &.vs-sidebar-group-open {
          >.group-header {
            background: $content-dark-bg;
          }
        }
      }
    }
  }

  // Sidebar Perfect Scrollbar
  .ps {
    &:hover {
      &>.ps__scrollbar-y-rail {
        &:hover {
          background-color: $dark-card-color;
        }
      }
    }
  }

  // Grid
  .grid-demo__layout-container {
    .vs-row {
      .vs-col {
        background-color: $table-dark-stripe;

        &:last-of-type {
          background-color: $table-dark-stripe;
        }
      }
    }
  }

  .grid-demo__layout-container--block {
    .vs-row {
      background-color: $table-dark-stripe;
    }
  }

  // Chat card of ecommerce page
  .chat-card-log {
    .flex-row-reverse {
      .msg {
        &.chat-sent-msg {
          background-color: $sidebar-dark-bg !important;
        }
      }
    }

    .msg {
      background-color: $table-dark-stripe !important;
    }
  }

  .chat-input-container,
  .chat__input {
    background-color: $dark-card-color !important;
    border-top: 1px solid $table-dark-stripe;
  }

  // Chat App
  #chat-app {
    .chat__profile-search {
      .vs-inputx {
        border-color: $theme-dark-border-color !important;
      }
    }

    #chat-list-sidebar {
      .chat-scroll-area {
        .chat__contact:hover {
          background: $dark-card-color;
        }
      }
    }

    .chat__header {
      header {
        background: $table-dark-stripe !important;

        h6 {
          color: $theme-dark-heading-color;
        }
      }
    }

