      .select-none {
        background-color: $table-dark-stripe !important;
      }

      h4 {
        background-color: $table-dark-stripe !important;
        color: $theme-dark-heading-color;
      }

      .msg {
        background-color: $theme-dark-bg !important;
        border-color: $grey-light !important;
      }

      &.chat-content-area {
        .chat-content-scroll-area {
          background: rgba($black, 0.2);
        }

        .chat__log {
          .msg-time {
            .vs-divider--text {
              background: $theme-dark-bg !important;
              border-color: $grey-light !important;
            }
          }
        }
      }

      .chat__input {
        input {
          border-color: $theme-dark-border-color !important;
        }
      }
    }
  }

  // ToDo
  #todo-app {
    .todo-list {
      h6 {
        &.todo-title {
          color: $theme-dark-heading-color;
        }
      }

      .todo-tags {
        .con-vs-chip {
          background: $dark-card-color;
          color: $theme-dark-text-color !important;
        }
      }
    }

    .list-item-component {
      border-color: $theme-dark-border-color;

      &:hover {
        box-shadow: 0px 0 0 0px $dark;
      }
    }
  }

  // Email
  #email-app {
    .email__mails {
      .email__mail-item {
        .mail__mail-item {

          background: $theme-dark-bg;

          &:hover {
            box-shadow: 10px 0 0 0px $dark;
          }

          &.mail__opened-mail {
            background: $theme-light-dark-bg;
          }
        }

        &:not(:first-of-type) {
          .mail__mail-item {
            border-top: 1px solid #414561;
          }
        }
      }
    }

    .email-view-sidebar {
      .vs-sidebar {
        background-color: $table-dark-stripe !important;
        border-left-color: #414561;
      }

      .email-header {
        border-bottom-color: #414561;
      }
    }
  }

  // Calendar
  #calendar-app {
    .full-calendar-body {
      .week-row {
        .day-cell.not-cur-month {
          .day-number {
            color: rgba(255, 255, 255, 0.24);
          }
        }
      }
    }
  }

  // Alert
  .vs-alert {
    code {
      color: $grey;
      background: $content-dark-bg;
    }
  }

  // Avatar
  .con-vs-avatar {
    background: $theme-dark-bg !important;

    // avatar color
    &.con-vs-avatar-primary {
      background: rgba(var(--vs-primary), 1) !important;
    }

    &.con-vs-avatar-success {
      background: rgba(var(--vs-success), 1) !important;
    }

    &.con-vs-avatar-danger {
      background: rgba(var(--vs-danger), 1) !important;
    }

    &.con-vs-avatar-warning {
      background: rgba(var(--vs-warning), 1) !important;
    }

    &.con-vs-avatar-info {
      background: rgba(var(--vs-info), 1) !important;
    }

    &.con-vs-avatar-dark {
      background: rgba($grey, 1) !important;
    }
  }

  // Profile
  #profile-page {
    .profile-header {
      .profile-header-nav {
        background: $dark-card-color !important;
      }
    }
  }

  // FAQ
  #faq-page {
    .faq-bg {
      background-color: $dark-card-color;
    }
  }


  // breadcrumb
  .vs-breadcrumb--ol {

    a,
    .vs-breadcrum--separator {
      color: $grey-light;
    }
  }

  // Button
  // .vs-button-dark {
  //   &.vs-button-filled {
  //     background: $grey !important;

  //     .vs-button--text {
  //       color: $dark;
  //     }
  //   }
  // }

  // .demo-text-dark {
  //   .vs-button-text {
  //     color: $grey;
  //   }
  // }

  // .vs-button-dark {

  //   &.vs-button-border,
  //   &.vs-button-flat,
  //   &.vs-button-line {
  //     border-color: $grey;
  //   }

  //   .vs-button--text,
  //   .vs-icon {
  //     color: $grey;
  //   }
  // }

  // Chip
  .con-vs-chip {
    background: $theme-dark-bg;
    color: $theme-dark-text-color !important;

    // CHIP COLOR
    &.vs-chip-primary {
      background: rgba(var(--vs-primary), 1);
    }

    &.vs-chip-success {
      background: rgba(var(--vs-success), 1);
    }

    &.vs-chip-danger {
      background: rgba(var(--vs-danger), 1);
    }

    &.vs-chip-warning {
      background: rgba(var(--vs-warning), 1);
    }

    &.vs-chip-info {
      background: rgba(var(--vs-info), 1);
    }

    &.vs-chip-dark {
      background: rgba(var(--vs-dark), 1);
    }

    .con-vs-avatar {
      background-color: $theme-light-dark-bg !important;
    }

    // white text color for colored chips
    &.con-color {
      color: $white !important;
    }
  }

  .con-chips {
    .con-chips--input {
      background-color: $theme-light-dark-bg;
    }
  }

  // Divider
  .vs-divider {
    .vs-divider-border {
      border-color: $theme-dark-border-color !important;

      &.vs-divider-border-primary {
        border-color: rgba(var(--vs-primary), 1) !important;
      }

      &.vs-divider-border-success {
        border-color: rgba(var(--vs-success), 1) !important;
      }

      &.vs-divider-border-danger {
        border-color: rgba(var(--vs-danger), 1) !important;
      }

      &.vs-divider-border-warning {
        border-color: rgba(var(--vs-warning), 1) !important;
      }

      &.vs-divider-border-info {
        border-color: rgba(var(--vs-info), 1) !important;
      }

      &.vs-divider-border-dark {
        border-color: rgba($grey, 0.5) !important;
      }
    }

    .vs-divider--text {
      // background: transparent;
      // color: $theme-dark-text-color !important;

      .vs-icon {
        // color: $theme-dark-text-color;
      }
    }
  }

  // Dropdown
  .vs-dropdown--menu,
  .vs-dropdown--menu--after {
    background: $content-dark-bg;
    border-color: $theme-dark-border-color;

    .con-dropdown--group-ul {
      background: $content-dark-bg;
    }

    .vs-dropdown--item {
      .vs-dropdown--item-link.disabled {
        color: $grey !important;
      }
    }
  }

  // Loading
  .con-vs-loading {
    background: rgba($dark-card-color, .6);

    h4.title-loading {
      color: $theme-dark-text-color;
    }
  }

  // List
  .vs-list {
    .vs-list--header {
      box-shadow: 0 7px 7px -5px rgba($theme-dark-shadow-color, 1);
    }

    .vs-list--item {
      border-color: rgba($theme-dark-text-color, .08);
    }
  }

  // Navbar
  .vs-navbar {
    border-color: $content-dark-bg;

    li {
      &.vs-navbar--item {
        a {
          color: $grey-light;
        }

        &.is-active-item {
          a {
            color: $white;
          }
        }
      }
    }
  }

  .vs-navbar {
    &.vs-navbar-color-transparent:not(.vs-navbar-flat) {
      background-color: $theme-dark-bg !important;
      box-shadow: 0 4px 20px 0 rgba(0, 0, 0, .5);
    }
  }

  // Pagination
  .vs-pagination--nav {
    .vs-pagination--ul {
      background: $theme-dark-bg;

      .vs-pagination--li {
        color: $theme-dark-text-color;
      }

      .item-pagination {
        &.is-current {
          color: $white;
        }
      }
    }

    .vs-pagination--buttons {
      background: $theme-dark-bg;
      color: $theme-dark-text-color;
    }
  }

  // Popup
  .con-vs-popup {
    .vs-popup {
      background: $content-dark-bg !important;
      color: $theme-dark-text-color;

      .vs-popup--header {
        background: $dark-card-color !important;

        .vs-popup--close {
          background: $content-dark-bg !important;
          color: $theme-dark-text-color;
        }
      }
    }
  }

  #popup-demo {
    .demo-alignment {
      .vs-button {
        background: $content-dark-bg !important;

        &:hover {
          box-shadow: $content-dark-bg 0px 8px 25px -8px !important;
        }
      }
    }
  }

  // Sidebar component
  .vs-sidebar {
    background: $sidebar-dark-bg;

    h4,
    h5 {
      color: $theme-dark-heading-color;
    }

    .vs-sidebar--header {
      border-bottom-color: $theme-dark-border-color;
    }

    .vs-sidebar--items {
      .vs-sidebar--item {
        a {
          color: $theme-dark-text-color;
        }
      }
    }

    .vs-sidebar-group {
      &.vs-sidebar-group-active {
        >.group-header {
          background: $content-dark-bg;
        }
      }
    }
  }

  // Slider
  .con-vs-slider {
    .vs-slider {
      background: rgba($content-dark-bg, 0.5);
    }
  }

  // Tabs
  .ul-tabs {
    .vs-tabs--li {
      button {
        color: $theme-dark-text-color;
      }
    }
  }

  .vs-tabs-dark {

    .activeChild button,
    button:not(:disabled):hover {
      color: $grey !important;
    }

    .line-vs-tabs {
      background: linear-gradient(30deg, rgba($grey, 1), rgba($grey, .5)) !important;
      box-shadow: 0 0 8px 0 rgba($grey, .4) !important;
    }
  }

  // Tooltip
  .vs-tooltip {
    &.vs-tooltip-dark {
      background: $content-dark-bg;
    }
  }

  // Upload
  .con-upload {

    .con-input-upload,
    .con-img-upload {
      background-color: $content-dark-bg;
    }

    .btn-upload-all {
      background-color: $sidebar-dark-bg;
    }

    .con-input-upload {
      border-color: $theme-dark-border-color;
    }
  }


  // Form Fields
  input {
    background: $theme-dark-bg;
    color: $theme-dark-text-color;

    ~.vs-placeholder-label,
    ~.vs-input--placeholder {
      color: $theme-dark-text-color;
    }
  }

  ::placeholder {
    color: $theme-dark-text-color !important;
  }

  .vs-input--icon {
    color: $theme-dark-text-color;
    border-right-color: rgba($theme-dark-text-color, .2);

    &.icon-after {
      border-left-color: rgba($theme-dark-text-color, .2);
    }
  }

  .vs-input-number {
    background: $content-dark-bg;

    button {

      &.vs-input-number--button-less,
      &.vs-input-number--button-plus {

        &:disabled,
        &.limit {
          opacity: 0.75;
          background: $grey;
        }
      }
    }
  }

  .vs-con-textarea {
    background: $content-dark-bg;
    color: $theme-dark-text-color;

    .vs-textarea {
      color: $theme-dark-text-color;

      &:focus {
        border-color: transparent;
      }
    }

    &.focusx {
      border-color: transparent;

      h4 {
        background: transparent;
      }
    }

    &.textarea-danger {
      .vs-textarea {
        color: rgba(var(--vs-danger), 1);
      }
    }
  }

  .vs-switch {
    background: $content-dark-bg;

    &.vs-switch-primary {
      &.vs-switch-active {
        background: rgba(var(--vs-primary), 1);
      }
    }

    &.vs-switch-success {
      &.vs-switch-active {
        background: rgba(var(--vs-success), 1);
      }
    }

    &.vs-switch-danger {
      &.vs-switch-active {
        background: rgba(var(--vs-danger), 1);
      }
    }

    &.vs-switch-warning {
      &.vs-switch-active {
        background: rgba(var(--vs-warning), 1);
      }
    }

    &.vs-switch-info {
      &.vs-switch-active {
        background: rgba(var(--vs-info), 1);
      }
    }

    &.vs-switch-dark {
      &.vs-switch-active {
        background: $grey;
      }
    }
  }

  .vs-radio-dark {
    .vs-radio--circle {
      background: $grey;
      box-shadow: 0 3px 12px 0 rgba($grey, .4);
    }
  }

  .op-block {
    box-shadow: 1px 1px 10px rgba($white, .1)
  }

  .vs-input-dark {
    .vs-input--input {
      &:focus {
        border-color: $grey !important;

        ~.vs-input--placeholder {
          color: $grey;
        }
      }
    }
  }

  .vue-form-wizard {
    .wizard-icon-circle {
      background: $table-light-stripe;
      border-color: $table-dark-stripe
    }

    .wizard-nav-pills {
      &>li {
        &>a {
          color: $white;

          .stepTitle {
            color: $grey-light;
          }

          &:hover {
            color: $white;
          }
        }
      }
    }

    .wizard-navigation {
      .wizard-nav {
        .stepTitle {
          color: $grey-light;
        }
      }
    }
  }

  // Tables
  .vs-con-table {
    .vs-con-tbody {
      background: $table-dark-stripe;
      border: 2px solid $content-dark-bg;

      .vs-table--tbody-table {
        tr {
          background: $table-light-stripe;
        }

        .vs-table--thead {
          tr {
            background: $table-dark-stripe;
          }

          // .con-td-check{
          //     background: $table-dark-stripe;
          // }
        }
      }
    }

    .con-edit-td {
      background: $table-dark-stripe;
    }

    .is-selected {
      .tr-values {
        background: $table-dark-stripe !important;
      }
    }
  }

  // Charts : Apex Chart
  .apexcharts-canvas {
    .apexcharts-tooltip {
      &.light {
        background: $table-dark-stripe;
        border-color: $table-light-stripe;

        .apexcharts-tooltip-title {
          background: $table-dark-stripe;
        }
      }
    }

    .apexcharts-xaxistooltip {
      background: $table-dark-stripe;
      border-color: $table-light-stripe;
      color: $white;

      &:before,
      &:after {
        border-bottom-color: $table-dark-stripe;
      }
    }

    .apexcharts-yaxistooltip {
      background: $table-dark-stripe;
      border-color: $table-light-stripe;
      color: $white;

      &:before,
      &:after {
        border-left-color: $table-dark-stripe;
      }
    }

    text {
      fill: $white !important;
    }

    .apexcharts-pie-series {
      path {
        stroke: $table-light-stripe;
      }
    }

    .apexcharts-legend {
      .apexcharts-legend-series {
        .apexcharts-legend-text {
          color: $grey !important;
        }
      }
    }

    .apexcharts-toolbar {
      .apexcharts-menu {
        background: $table-light-stripe;
        border-color: $table-light-stripe;

        .apexcharts-menu-item {
          background: $table-dark-stripe;
        }
      }
    }

    .apexcharts-radar-series {
      polygon {
        fill: $content-dark-bg;
        stroke: $table-dark-stripe;
      }
    }

    .apexcharts-track {
      path {
        stroke: $table-light-stripe;
      }
    }

    .apexcharts-selection-icon:not(.selected):hover svg,
    .apexcharts-zoom-icon:not(.selected):hover svg,
    .apexcharts-zoom-in-icon:hover svg,
    .apexcharts-zoom-out-icon:hover svg,
    .apexcharts-reset-zoom-icon:hover svg,
    .apexcharts-menu-icon:hover svg {
      fill: $white;
    }

    // grid color
    .apexcharts-gridline {
      stroke: $theme-dark-border-color;
    }
  }

  // Colors
  .token.operator,
  .token.entity,
  .token.url,
  .language-css .token.string,
  .style .token.string {
    background: transparent;
  }

  // Seach
  .search-tab-filter {
    background: $dark-card-color;
  }

  // Select
  .vs-select--options {
    background: $dark-card-color;
    border-color: rgba($grey, 0.2);

    span {
      color: $theme-dark-text-color;
    }

    .vs-select--item {
      color: $grey;
      border-color: rgba($grey, 0.2);
      box-shadow: none;

      &:hover {
        background: $content-dark-bg;
        // color: $grey;
      }
    }
  }

  // Quill editor
  .quill-editor {
    .ql-toolbar {
      button {
        color: $white;

        svg {
          path {
            stroke: $white;
          }
        }
      }

      .ql-fill {
        fill: $white;
      }

      .ql-stroke {
        stroke: $white;
      }

      .ql-picker {
        color: $white;

        .ql-picker-options {
          background: $dark-card-color;
        }
      }
    }

    .ql-editor {
      &.ql-blank {
        &::before {
          color: rgba($white, .6);
        }
      }
    }
  }

  .activity-timeline {
    border-color: $theme-dark-border-color;
  }

  // Datepicker
  .vdp-datepicker {
    .vdp-datepicker__calendar {
      background: $content-dark-bg;
      border-color: $theme-dark-border-color;

      header {

        .prev,
        .next,
        .up {
          &:not(.disabled) {
            &:hover {
              background: $dark-card-color;
            }
          }
        }
      }
      .disabled {
        color: rgba($grey, .6);
      }

      .cell {
        .highlighted {
          background: $dark-card-color;
        }

        &:hover {
          background-color: $theme-dark-bg;
        }
      }
    }

    input {
      border: 0;
      padding: 10px;
    }
  }

  // Collapse
  .vs-collapse {
    &.shadow {
      box-shadow: 0 0px 10px 1px rgba($theme-dark-shadow-color, 1) !important;
    }

    .vs-collapse-item {
      border-bottom-color: rgba($white, .04);
    }

    &.border {
      border-color: rgba($white, .1);

      .vs-collapse-item {
        border-bottom-color: rgba($white, .04);
      }
    }

    &.margin {
      .vs-collapse-item {
        box-shadow: 0 2px 10px 0 rgba($theme-dark-shadow-color, 1)
      }
    }
  }

  // vx-auto-suggest
  .vx-auto-suggest {
    .auto-suggest-suggestions-list {
      background-color: $content-dark-bg !important;
    }
  }

  // footer layouts
  .footer-sticky {
    .the-footer {
      background-color: $dark-card-color;
      color: $white !important;
    }
  }

  // Dialogs - This dialog need a dark layout claas
  .con-vs-dialog {
    .vs-dialog {
      background: $content-dark-bg;
      color: $white;

      header {
        background: $dark-card-color;

        .con-title-after {
          h3 {
            color: $white;
          }
        }

        .vs-dialog-cancel,
        .vs-icon {
          background: $content-dark-bg;
          color: $white;
        }
      }

      footer {
        .vs-button--text {
          color: $white;
        }
      }

      .vs-dialog-text {
        .date-label {
          color: $white;
        }
      }

      input,
      .vs-con-textarea {
        background: $dark-card-color;
      }

      .con-upload {

        .con-input-upload,
        .con-img-upload {
          background: $dark-card-color;
        }
      }

      .quill-editor {
        background: $table-light-stripe;
      }
    }
  }

  .tree-container {
    border-color: $theme-dark-border-color;

    .tag,
    .search-input {
      border-color: $theme-dark-border-color;
    }
  }

  .halo-tree {
    .node-title {
      &:hover {
        background-color: $theme-light-dark-bg;
      }
    }
  }

  // Customizer
  #theme-customizer {
    input {
      background: $theme-light-dark-bg;
    }
  }

  .add-new-data-sidebar {
    input {
      background: $theme-light-dark-bg;
    }
  }


  // //////////////////////////////////////////////////////////////////////////////////////////////
  // STYLE IF COMPONENT IS INSIDE CARD (Any wrapper which have dark $theme-dark background color)
  // //////////////////////////////////////////////////////////////////////////////////////////////

  .vx-card {

    // PAGINATION
    .vs-pagination--nav {
      .vs-pagination--ul {
        background: $theme-light-dark-bg;

        .vs-pagination--li {
          color: $white;
        }
      }

      .vs-pagination--buttons {
        background: $theme-light-dark-bg;
        color: $white;
      }
    }

    // AVATAR
    .con-vs-avatar {
      background: $theme-light-dark-bg !important;

      // avatar color
      &.con-vs-avatar-primary {
        background: rgba(var(--vs-primary), 1) !important;
      }

      &.con-vs-avatar-success {
        background: rgba(var(--vs-success), 1) !important;
      }

      &.con-vs-avatar-danger {
        background: rgba(var(--vs-danger), 1) !important;
      }

      &.con-vs-avatar-warning {
        background: rgba(var(--vs-warning), 1) !important;
      }

      &.con-vs-avatar-info {
        background: rgba(var(--vs-info), 1) !important;
      }

      &.con-vs-avatar-dark {
        background: rgba($grey, 1) !important;
      }
    }

    // CHIP
    .con-vs-chip {
      background: $theme-light-dark-bg;

      // CHIP COLOR
      &.vs-chip-primary {
        background: rgba(var(--vs-primary), 1);
      }

      &.vs-chip-success {
        background: rgba(var(--vs-success), 1);
      }

      &.vs-chip-danger {
        background: rgba(var(--vs-danger), 1);
      }

      &.vs-chip-warning {
        background: rgba(var(--vs-warning), 1);
      }

      &.vs-chip-info {
        background: rgba(var(--vs-info), 1);
      }

      &.vs-chip-dark {
        background: rgba(var(--vs-dark), 1);
      }

      .con-vs-avatar {
        background-color: $theme-dark-bg !important;
      }
    }

    .con-chips {
      .con-chips--input {
        background-color: $theme-dark-bg !important;
      }
    }

    // NAVBAR
    .vs-navbar {
      &.vs-navbar-color-transparent {
        background-color: $theme-light-dark-bg !important;
      }

      .vs-navbar--btn-responsive {
        .btn-responsive-line {
          background: $theme-dark-text-color;
        }
      }
    }

    .vs-navbar-border
    {
      border-color: $theme-dark-border-color;

      .vs-navbar--item {
        border-color: $theme-dark-border-color;
      }
    }

    .vs-navbar-shadow {
      .vs-navbar--item {
        &.is-active-item {
          background-color: $theme-dark-bg;
        }
      }
    }

    // INPUT
    input {
      background: $theme-light-dark-bg;
    }
  }
}


// RTL Fixes for dark mode
/*! rtl:begin:ignore */
.theme-dark {
  .vdp-datepicker {
    .vdp-datepicker__calendar {
      header {
        .prev {
          &:after {
            border-right-color: $white;
          }
        }

        .next {
          &:after {
            border-left-color: $white;
          }
        }
      }
    }
  }
}
/*! rtl:end:ignore */
